html {
  margin: 0;
}

body {
  background-color: #fff;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
}

.page {
  grid-template-columns: repeat(1, auto);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .page {
    grid-template-columns: repeat(2, auto);
  }
}

@media (width >= 768px) {
  .page {
    grid-template-columns: repeat(3, auto);
  }
}

@media (width >= 1024px) {
  .page {
    grid-template-columns: repeat(4, auto);
  }
}

@media (width >= 1200px) {
  .page {
    grid-template-columns: repeat(4, auto);
  }
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav {
  align-items: center;
  height: 60px;
  display: flex;
}

.nav__list {
  justify-content: space-between;
  align-items: center;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  height: 100%;
  margin-right: 20px;
}

.nav__item:last-child {
  margin: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  white-space: nowrap;
  height: 100%;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.nav__link:hover {
  color: #00acdc;
}

.nav:last-child {
  margin: 0;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  right: 0;
}

.card {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: flex;
}

.card__img {
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__product-name {
  color: #060b35;
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: block;
}

.card__stars-n-reviews {
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 16px;
  display: flex;
}

.card__reviews {
  text-align: right;
  color: #060b35;
  flex-direction: column;
  justify-content: flex-end;
  height: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 24px;
  margin-bottom: 0;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-number {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.card:hover {
  scale: 1.2;
}

.card:hover .card__product-name {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__star:last-child {
  margin-right: 0;
}

